  <div class="filter w1200px">
    <div class="tips" v-if="showTips"><p>924!</p></div>
    <div class="item clearfix" v-for="(item, name, k) in filterData" :key="k">
      <div class="left">
        <span class="t">{{ name }}</span>
        <span></span>
      </div>
      <div class="right">
        <span v-for="(text, i) in item" :key="i" @click="filterClick(name, text.id)"> <i></i> {{ text.text }}</span>
      </div>
      
    </div>
    <div class="sort">
      <span class="active"></span>
      <span></span>
      <span></span>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    category: String
  },
  computed: {
    showTips () {
      return this.$route.params.name === 'renting'
    },
    filterData () {
      const data = {
        '': {
          '': [
            {
              text: '',
              id: 'east'
            },
            {
              text: '',
              id: 'west'
            },
            {
              text: '',
              id: 'urban'
            },
            {
              text: '',
              id: 'middle'
            },
            {
              text: '',
              id: 'east'
            },
            {
              text: '',
              id: 'north'
            },
            {
              text: '',
              id: 'east_north'
            },
            {
              text: '',
              id: 'other'
            }
          ],
          '': [
            {
              text: '100',
              id: 1
            },
            {
              text: '100-200',
              id: 2
            },
            {
              text: '200-500',
              id: 3
            },
            {
              text: '500',
              id: 4
            }
          ],
          '': [
            {
              text: '50',
              id: '1'
            },
            {
              text: '50-70',
              id: '2'
            },
            {
              text: '70-100',
              id: '3'
            },
            {
              text: '100-150',
              id: '4'
            },
            {
              text: '150',
              id: '5'
            }
          ],
          '': [
            {
              text: '',
              id: '1'
            },
            {
              text: '',
              id: '2'
            },
            {
              text: '+',
              id: '3'
            },
            {
              text: '',
              id: '4'
            },
            {
              text: '+',
              id: '5'
            },
            {
              text: '+',
              id: '6'
            },
            {
              text: '',
              id: '7'
            },
            {
              text: '+',
              id: '8'
            },
            {
              text: '',
              id: '9'
            }
          ]
        },
        '': {

        }
      }
      return data[this.category]
    }
  },
  methods: {
    filterClick (type, id) {
      const data = {
        '': 'region',
        '': 'price',
        '': 'area',
        '': 'house'
      }
      type = data[type]
      let query = this.$route.query
      query[type] = query[type] ? query[type].split(',') : []
      query[type].push(id)
      query[type] = query[type].toString()
      const path = this.$route.path
      this.$router.push({
        path: `${path}?${query}`
      })
    }
  }
}
</script>
